import {
    defineStore
} from "pinia";
import {
    Socket
} from "@/utils/websocketUtils";
export const useSocketStore = defineStore("websockets", {
    state: () => {
        return {
            wsConfig: null,
        }
    },
    getters: {

    },
    actions: {
        createWebSocket(data) {
            this.wsConfig = {
                url: data.url,
                data: {},
                message: () => {},
                fail: err => {
                    console.log("错误", err)
                },
                close: () => {}
            }
            this.wsConfig.ws = new Socket(this.wsConfig)
        }
    }

})