import {
    message
} from 'ant-design-vue'

function errorMsg(code) {
    let msgList = {
        10001: "错误"
    }
    return msgList[code]
}
export class Socket {
    ws = null; //websocket实例
    url = null; //websocket地址
    wsconfig = null; //请求配置参数
    event = null; //方法
    heartbeatTime = null; //心跳计时器
    rec = null; //重连计时器

    constructor(params) {
        // 判断浏览器是否支持,如果不支持，则进行提示
        if (!(window.WebSocket || window.MozWebSocket)) {
            // Message({
            //     type: "error",
            //     message: 
            // });
            message.error("抱歉，您的浏览器不支持webSocket")
            return false;
        }
        this.url = params.url;
        this.wsconfig = params.data;
        this.event = params
        this.connect()
    }
    connect() {
        this.ws = new WebSocket(this.url)
        this.ws.onopen = this.connectOpen; // websocket打开时触发
        this.ws.onmessage = this.message; // 获取到推送信息触发
        this.ws.onerror = this.error; // 通信错误时触发
        this.ws.onclose = this.close; // 连接取消时触发
        return Promise.resolve(this.ws);

    }
    connectOpen = res => {
        let state = this.ws.readyState;
        let self = this
        self.event.open && self.event.open(state, res);
        if (state == 0) {
            self.event.fail && self.event.fail("连接尚未建立");
        }
        // 连接成功
        else if (state == 1) {
            self.event.success && self.event.success(state);
            this.heartbeat()
        }
        // 连接正在进行关闭 
        else if (state == 2) {
            self.event.fail && self.event.fail("连接正在进行关闭");
        }
        // 连接已关闭或不能打开
        else if (state == 3) {
            self.event.fail && self.event.fail("连接已关闭或不能打开");
        }
    }
    //获取到推送消息
    message = msg => {
        this.event.message && this.event.message(msg)
    }
    //获取到错误消息
    error = err => {
        this.event.fail && this.event.fail(err);
    }
    // 关闭websocket触发
    close = event => {
        this.event.close && this.event.close(event);
    }
    //重连
    reconnection() {

        this.rec && clearTimeout(this.rec)
        this.rec = setTimeout(() => {
            this.connect()
        }, 5000)
        // 延迟5秒重连  避免过多次过频繁请求重连

    }
    //心跳
    heartbeat = () => {
        clearInterval(this.heartbeatTime)
        this.ws.send('ping')
        this.heartbeatTime = setInterval(() => {
            this.ws.send('ping')
        }, this.data.heartbeat)
    }
    //手动发送信息
    manualSend = data => {
        console.log("发送")
        if (this.ws.readyState != 1) {
            this.error({
                readyState: this.ws.readyState,
                msg: "当前连接不处于通信状态"
            });
            return false;
        }
        this.ws.send(JSON.stringify(data));
        return true;
    }
    /** 手动取消 */
    manualClone = () => {
        this.ws && this.ws.close && this.ws.close();
        clearInterval(this.heartbeatTime);
    };

}